#Document-top-container {
    text-align: center;
    background-color: #282c34;
    min-height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: calc(12px + 1vmin);
    color: white;
}

#Company-logo {
    height: 10vmin;
    pointer-events: none;
}

#Document-top-text {
    margin-block-start: 5px;
    margin-block-end: 5px;
}

.Document-top-buttons {
    height: 100%;
    text-align: center;
    background-color: #282c34;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.Document-top-button {
    height: 50px;
    padding: 0 20px 0 20px !important;
    font-size: calc(12px + 1vmin) !important;
    font-weight: 400 !important;
    text-transform: capitalize !important;
    color: white !important;
}